<template>
  <div class="home">
    <a-collapse accordion>
      <a-collapse-panel v-for="item in directory" :key="item.key" :header="item.key">
        <a-list :data-source="item.files">
          <a-list-item slot="renderItem" slot-scope="file">
            <span class="home-list-item" @click="go(file)">
              {{ file.replace(`./${item.key}/`, '') }}
            </span>
          </a-list-item>
        </a-list>
      </a-collapse-panel>
    </a-collapse>

    <div id="components-back-top-demo-custom">
      <a-back-top>
        <div class="ant-back-top-inner">
          UP
        </div>
      </a-back-top>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'Home',
  data() {
    const pageArr = require.context('@/mdPages', true, /\.md$/).keys()
    const directory = pageArr.reduce((prev, next) => {
      const key = next.split('/')[1]
      const item = prev.find(item => item.key === key)
      if (item) {
        item.files.push(next)
      } else {
        prev.push({
          key,
          files: [next]
        })
      }
      return prev
    }, [])
    return {
      directory
    }
  },
  methods: {
    go(item) {
      this.$router.push({
        name: encodeURI(item.replace('./', ''))
      })
    }
  },
  components: {
    
  }
}
</script>

<style scoped lang="less">
.home {
  &-list-item {
    cursor: pointer;
  }
}
</style>