import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const pageArr = require.context('@/mdPages', true, /\.md$/).keys().map(item => item.replace('./', '@/mdPages/'))
let mdRoutes = pageArr.map(item => {
  const path = encodeURI(item.replace('@/mdPages/', ''))
  return {
    path,
    name: path,
    component: () => new Promise((resolve, reject) => {
      const Com = require(`../mdPages/${item.replace('@/mdPages/', '')}`)
      resolve(Com)
    })
  }
})
console.log(mdRoutes)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/md',
    name: 'md',
    component: () => import('@/views/Md.vue'),
    children: mdRoutes
  }
]

const router = new VueRouter({
  routes
})

export default router
